body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.App {
    margin: 0 auto;
    height: 100vh;
    background-position: center;
    background-repeat: no-repeat;
    background-size: cover;
    background-image: url(/metro/static/media/metro_map.acaebabb.jpg);
    text-align: center;
    font-family: "Helvetica Neue";
    display: grid;
    grid-template-columns: repeat(2, auto);
    grid-template-rows: 80px auto 1fr auto;
}

.header { 
    /* in App */
    grid-column: 1/-1;
    grid-row: 1/2;
    background-color: #3e3e3e;
    color: white;
    padding: 10px;
    /* in header */
    display: grid;
    grid-template-columns: 1fr auto 1fr;
    grid-template-rows: repeat(2, auto);
}

h1 {
    margin: 0px;
}

.whereTrain {
    font-size: 28px;
    grid-column: 2/3;
    grid-row: 1/2;
}

.infoGuide {
    grid-column: 2/3;
    grid-row: 2/3;
}

.stations {
    /* in App */
    grid-column: 1/-1;
    grid-row: 2/3;
    color: white;
    background-color: #4679af;
    padding: 20px;
}

#apiNote {
    font-size: 8pt;
    padding: 2px;
}

button {
    border: 1px solid black;
    border-radius: 5px;
    margin: 5px;
    padding: 5px;
}

.dropdown {
    border: 1px solid black;
    border-radius: 5px;
    margin: 5px;
    padding: 5px;
}

.results {
    /* in App */
    grid-column: 1/-1;
    grid-row: 3/4;
    justify-self: center;
    padding-top: 10px;
    /* in results */
    display: grid;
    grid-template-columns: 40px repeat(3, 1fr) 40px;
    grid-template-rows: 1fr;
}

.resultsInner {
    grid-column: 2/span 3;
}

.footer {
    /* in App */
    grid-column: 1/-1;
    grid-row: 4/-1;
    background-color: #3e3e3e;
    color: white;
    font-size: 8pt;
    padding: 10px;
    /* in footer */
    display: grid;
    grid-template-columns: repeat(5, 1fr);
    grid-template-rows: auto;
}

#metroLogo {
    width: 25px;
    grid-column: 2/3;
    grid-row: 1/-1;
}

#power {
    grid-column: 3/4;
    grid-row: 1/-1;
    padding-top: 3px;
}

a {
    color: white;
}
.train {
    width: auto;
    border-radius: 25px;
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    grid-template-rows: auto;
    justify-self: center;
    font-size: 16px;
    background-color: white;
    margin-bottom: 5px;
    border: 5px solid black;
    font-family: "Helvetica Neue";
}

p {
    margin: 0px;
}

#destination {
    grid-column: 1/2;
    grid-row: 1/-1;
    padding: 5px;
}

#arrival {
    grid-column: 2/3;
    grid-row: 1/-1;
    padding: 5px;
    border-left: 0.5px solid #3e3e3e;
    border-right: 0.5px solid #3e3e3e;
}

#cars {
    grid-column: 3/4;
    grid-row: 1/-1;
    padding: 5px;
}

.train_BL {
    border: 10px solid #007bc0;
}

.train_RD {
    border: 10px solid #e33144;
}

.train_YL {
    border: 10px solid #fed225;
}

.train_GR {
    border: 10px solid #00ab57;
}

.train_OR {
    border: 10px solid #f79631;
}

.train_SV {
    border: 10px solid #a3a5a2;
}

